lo extremo, un freno manual. Este freno se puede desplazar por toda la cuerda, en una sola dirección, lo cual reduce la longitud de agarre y permite que la persona que lo esté usando tenga libres sus manos y, por ende, pueda realizar su trabajo cómodamente y con seguridad. Se debe tener en cuenta que el extremo de la cuerda debe impedir que el freno manual se salga de ella, es decir, de la línea de posicionamiento. Esta línea, además, le permite a la persona que realiza el trabajo ubicarse en frente de la zona donde debe realizar la labor, dado que este elemento rodea toda la estructura y se une al arnés a través de las argollas laterales de posicionamiento, lo cual permite una estabilidad corporal.")])]),_c('div',{staticClass:"col-md-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/tema1-img-14.jpg"),"alt":"Texto que describa la imagen","data-aos":"fade-left"}})])])]),_c('div',{staticClass:"row justify-content-center my-lg-4"},[_c('div',{staticClass:"col-md-5 mb-4 mb-md-0"},[_c('h5',{staticClass:"mb-5"},[_vm._v("Salva caídas "),_c('em',[_vm._v("troll")])]),_c('p',[_vm._v("Este elemento es deslizante y puede desplazarse en un solo sentido. Adicionalmente, tiene doble traba de seguridad, lo cual permite que quien esté trabajando se asegure a la línea de vida, que recorre la ruta tanto de ascenso como de descenso. Este elemento se debe conectar al arnés del trabajador a través del uso de un mosquetón de seguridad. Como objetivo final de este elemento está la detención de la caída del trabajador a partir de un bloqueo automático sobre la línea de vida.")])]),_c('div',{staticClass:"col-md-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/tema1-img-15.jpg"),"alt":"Texto que describa la imagen","data-aos":"fade-left"}})])])]),_c('div',{staticClass:"row justify-content-center my-lg-4"},[_c('div',{staticClass:"col-md-5 mb-4 mb-md-0"},[_c('h5',{staticClass:"mb-5"},[_vm._v("Conector doble con absorbedor de choque")]),_c('p',[_vm._v("Es una línea de conexión doble, la cual cuenta con desacelerador. Este último debe tener dos cintas de poliamida. En cada uno de los extremos de estas cintas debe haber mosquetones de seguridad, los cuales tienen 60 milímetros de apertura, aproximadamente. Estos mosquetones se utilizan para anclarse a las estructuras o a aquellas partes en las que se requiera asegurarse. Además, hay un tercer mosquetón que está unido al punto de anclaje del arnés. También se debe considerar un sistema de disminución de velocidad o de absorción de energía, dado que la cinta se empieza a abrir en periodos de tiempo reposados, con el fin de que la caída se regule y, por lo tanto, la fuerza sea absorbida por este sistema y no por el cuerpo de la persona que lo utiliza.")])]),_c('div',{staticClass:"col-md-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/tema1-img-16.jpg"),"alt":"Texto que describa la imagen","data-aos":"fade-left"}})])])])])],1)]),_vm._m(10),_vm._m(11),_vm._m(12),_c('p',{staticClass:"mb-4"},[_vm._v("A continuación, se muestran los componentes de un sistema contra caídas:")]),_c('div',{staticClass:"tabsC-01 mb-5"},[_c('TabsC',{staticClass:"color-primario"},[_c('div',{staticClass:"py-3 py-md-4",attrs:{"titulo":"Anclajes"}},[_c('div',{staticClass:"row align-items-center"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/tema1-img-19.jpg"),"alt":"Texto que describa la imagen"}})])]),_c('div',{staticClass:"col-md-6"},[_c('p',[_vm._v("Están diseñados e instalados por personal idóneo, son resistentes ante la fuerza de una caída. Los anclajes son la base principal de equipos conectores, por consiguiente, deben tener la forma y tamaño adecuados. Además, deben ser estructuras a las que se les adecua un dispositivo de anclaje portátil. Pueden dividirse entre puntos fijos y puntos que permiten el desplazamiento del trabajador.")])])])]),_c('div',{staticClass:"py-3 py-md-4"